import { useState, useRef } from 'react'
import { Routes, Route, useLocation } from 'react-router-dom'
import { useTransition, animated } from 'react-spring'

import Welcome from './components/welcome/Welcome'
import Questions from './components/questions/Questions'
import Finish from './components/finish/Finish'

import './app.css'
import First from './components/questions/question/First'
import Second from './components/questions/question/Second'
import Third from './components/questions/question/Third'
import Fourth from './components/questions/question/Fourth'
import Fifth from './components/questions/question/Fifth'


function App() {

  const prevIndexRef = useRef(-1)
  const [activeIndex, setActiveIndex] = useState(0)
  const [checkboxValid, setCheckboxValid] = useState(false)
  const [data, setData] = useState({
    name: "",
    email: "",
    phoneNumber: "",
    jobTypeCheckbox: {
      "PR ": false,
      "SMM , ": false,
      "-": false,
      "  ": false,
      "Frontend-": false
    },
    participated: "",
    failures: "",
    ownProjects: "",
    books: "",
    courses: "",
    sources: "",
    expectionsIn5Years: "",
    expectedSalary: "",
    comfortableSalary: "",
    minimumSalaryForLife: "",
    whereToStart: "",
    howNotToDo: "",
    howToDo: "",
    anyQuestions: "",
  })

  const onFinish = () => {
    if (checkboxValid) {
      console.log({
        "": data.name,
        "email": data.email,
        "": data.jobTypeCheckbox,
        "   ()": data.participated,
        "": data.failures,
        " ": data.ownProjects,
        " ": data.books,
        "": data.courses,
        "  ()": data.sources,
        "  5 ": data.expectionsIn5Years,
        " ": data.expectedSalary,
        " ": data.comfortableSalary,
        " ": data.minimumSalaryForLife,
        "    ": data.whereToStart,
        "   ": data.howNotToDo,
        "  ": data.howToDo,
        "/": data.anyQuestions,
      })
    }
  }

  const location = useLocation()

  const transitions = useTransition(location, {
    from: {
      opacity: 0.8,
      transform: activeIndex > prevIndexRef.current ? `translateX(100%)` : `translateX(-100%)`
    },
    enter: {
      opacity: 1,
      transform: "translateX(0%)",
    },
    leave: {
      opacity: 0.8,
      transform: activeIndex > prevIndexRef.current ? `translateX(-100%)` : `translateX(100%)`
    },
    onRest: () => {
      prevIndexRef.current = activeIndex;
    }
  })

  return (
    <div className="slides">
      {transitions((props, item) => (
        <animated.div style={props} className="slide">
          <Routes location={item}>
            <Route path="/briefing" element={<Welcome activeIndex={activeIndex} setActiveIndex={setActiveIndex} />} />
            <Route path="/" element={<Questions title=" " />}>
              <Route path="/first" element={<First
                data={data}
                setData={setData}
                checkboxValid={checkboxValid}
                setCheckboxValid={setCheckboxValid}
                onFinish={onFinish}
                activeIndex={activeIndex}
                setActiveIndex={setActiveIndex} />} />
            </Route>
            <Route path="/" element={<Questions title=" " />}>
              <Route path="/second" element={<Second
                data={data}
                setData={setData}
                checkboxValid={checkboxValid}
                setCheckboxValid={setCheckboxValid}
                onFinish={onFinish}
                activeIndex={activeIndex}
                setActiveIndex={setActiveIndex} />} />
            </Route>
            <Route path="/" element={<Questions title="" />}>
              <Route path="/third" element={<Third
                data={data}
                setData={setData}
                checkboxValid={checkboxValid}
                setCheckboxValid={setCheckboxValid}
                onFinish={onFinish}
                activeIndex={activeIndex}
                setActiveIndex={setActiveIndex} />} />
            </Route>
            <Route path="/" element={<Questions title="" />}>
              <Route path="/fourth" element={<Fourth
                data={data}
                setData={setData}
                checkboxValid={checkboxValid}
                setCheckboxValid={setCheckboxValid}
                onFinish={onFinish}
                activeIndex={activeIndex}
                setActiveIndex={setActiveIndex} />} />
            </Route>
            <Route path="/" element={<Questions title="" />}>
              <Route path="/fifth" element={<Fifth
                data={data}
                setData={setData}
                checkboxValid={checkboxValid}
                setCheckboxValid={setCheckboxValid}
                onFinish={onFinish}
                activeIndex={activeIndex}
                setActiveIndex={setActiveIndex} />} />
            </Route>
            <Route path="/finish" element={<Finish />} />
          </Routes>
        </animated.div>
      ))}
    </div>
  )
}

export default App
